.messages__item {
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #fff;
    margin-bottom: 30px;
}

.messages__item:last-child {
    margin-bottom: 0;
}

.messages__receiver {
    margin-left: auto;
    background-color: #dcf8c6;
}

.message__timestamp {
    margin-left: 10px;
    font-size: xx-small;
}

.message__name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;    
}

